<template>
  <div class="wrap">
    <Banner :title="title" />
    <div ref="container" class="container" v-show="isLoad">
      <Good :payload="item" />
      <Operator class="ml4 font11" :isOperator="true" :payload="item" />
      <div class="hr"></div>
      <Price class="ml4 mt4" :payload="item" :isColumn="true" />
      <Phone :userInfo="user" ref="phone" />
      <div class="ek-meta good-detail pl4 pr4" v-html="item.goods_info_desc"></div>
      <Pay event="selectGoodsDetail" />
    </div>
    <div class="container" v-show="!isLoad">
      <div class="tc df cr666" style="margin-top: 3rem">{{ loadingNotice }}</div>
    </div>
  </div>
</template>
<script scoped>
  import { mapGetters } from 'vuex';
  import Banner from '../../components/banner';
  import Good from '../../components/good';
  import Price from '../../components/price';
  import Operator from '../../components/operator';
  import Phone from '../../components/phone';
  import Pay from '../../components/pay';

  export default {
    data () {
      return {
        title: '商品详情', count: 0, payload: {},
        item: {}, raw: {}, user: {}, isLoad: false, loadingNotice: '页面加载中···',
        params: {
          size: 10, exchange: 0,
        }
      }
    },
    components: {
      Banner, Good, Price, Operator, Phone, Pay
    },
    computed: {
      ...mapGetters({
        memory: 'getAllocateVariable',
      })
    },
    methods: {
      bubble (subscription) {
        this.$store.dispatch('bubbleDelegation', { subscription, page: this });
      },
      register (subscription) {
        this.$store.dispatch('registerCustomer', { register: subscription, page: this });
      },
      request (subscription, payload) {
        this.$store.dispatch('requestCustomer', { request: subscription, payload, page: this });
      },
      modifyCustom ({ target }) {
        this.custom.isFixed = target.scrollTop > this.custom.topFixed;
      },
      cancelNotice () {
        this.isNotice = false;
      },
      selectGoods (isSelected) {
        this.bubble('selectGoods');
      }
    },
    mounted () {
      this.register('resizepagepositon');
      this.bubble('selectGoodsDetail');

      this.$el.onclick = (e) => {
        if (!this.$refs.phone.$el.contains(e.target)) {
          document.activeElement.blur();
        }
      }
    },
    watch: {
      'memory.selectGoodsDetail' (counter) {
        if (counter != 0) {
          this.isLoad = true;
        }
      },
      'count' () {
        this.$refs.container.scrollTop = this.payload.forward < 0 ? 100 : 0;
      }
    }
  }
</script>
<style scoped>
  .good {
    margin: 1rem auto;
    transform: scale(1.2);
  }
  .operator {
    margin-bottom: .28rem;
  }
  .phone {
    margin-top: .4rem;
    margin-bottom: .4rem;
  }
  .pay {
    height: 2rem;
  }
</style>


