<template>
  <div class="wrap">
    <Banner :title="title" :payload="banner" @redirectOrderList="redirectOrderList" />
    <Notice v-if="isNotice" @cancelNotice="cancelNotice" :notice="notice" />
    <!-- 商品列表导航栏 -->
    <Custom
      :isFixed="true"
      v-show="isFixed" 
      :isSelected="isSelected"
      @exchangeGoodsList="exchangeGoodsList" />
    <!-- container -->
    <div ref="container" class="container" @scroll="mountCustom" v-show="isLoad">
      <Account ref="account" :payload="user" />
      <!-- 商品列表导航栏 -->
      <Custom
        ref="custom"
        :isFixed="false"
        :isSelected="isSelected"
        :style="isFixed ? { zIndex: -1 } : ''"
        @exchangeGoodsList="exchangeGoodsList" />
      <ul ref="items" class="items row pb4" v-show="items.length != 0">
        <li ref="itemNotice" class="tc pt4 pb4 cr666">{{ refresh }}</li>
        <li class="item col-mb-6" v-for="item in items" @click="redirectGoodsDetail(item.id)">
          <div class="item-content">
            <Good :payload="item" />
            <Operator :payload="item" class="operator font11" />
            <Price :payload="item" />
          </div>
        </li>
      </ul>
      <div class="tc" v-show="items.length == 0">
        <div class="empty"></div>
        <div class="font14 cr999">暂时没有你可以兑换的商品哦～</div>
      </div>
      <div class="cr666 tc pt4 pb4 mt4" v-show="items.length != 0">{{ footerNotice }}</div>
    </div>
    <div class="container" v-show="!isLoad">
      <div class="tc df cr666" style="margin-top: 3rem">{{ loadingNotice }}</div>
    </div>
  </div>
</template>
<script scoped>
  import { mapGetters } from 'vuex';
  import Banner from '../../components/banner';
  import Notice from '../../components/notice';
  import Account from '../../components/account';
  import Custom from '../../components/custom';
  import Good from '../../components/good';
  import Price from '../../components/price';
  import Operator from '../../components/operator';
  import Drag from '../../assets/js/Drag';

  export default {
    data () {
      return {
        title: '翼豆商城', isLoad: false, loadingNotice: '页面加载中···',
        // 兑换次数用尽提示
        isNotice: false, notice: '', isFresh: false, isRedirect: true,
        // 切换兑换商品导航
        isSelected: false, isSelecting: false, topFixed: 0, isFixed: false, isExtra: false,
        // 页面缓存数据
        raw: {}, items: [], order: {}, user: {},
        // 此页面对外跳转所需所有默认参数
        params: { size: 10, exchange: 0 },
        // 此页面顶部导航
        banner: { title: '兑换记录' },
        delegationEvent: '',
        footerNotice: '更多商品敬请期待～',
        refresh: '下拉加载更多～',
      }
    },
    components: {
      Banner, Notice, Account, Good, Price, Operator, Custom
    },
    computed: {
      ...mapGetters({
        memory: 'getAllocateVariable',
      })
    },
    methods: {
      bubble (subscription) {
        this.$store.dispatch('bubbleDelegation', { subscription, page: this });
      },
      request (subscription, payload) {
        this.$store.dispatch('requestCustomer', { request: subscription, payload, page: this });
      },
      // 商品切换导航吸顶
      mountCustom ({ target }) {
        this.isFixed = target.scrollTop > this.topFixed;
        const itemsTop = this.$refs.items.offsetHeight;
        const itemsViewTop = target.offsetHeight - this.$refs.custom.$el.offsetHeight - this.topFixed;
        const distance = itemsTop - itemsViewTop - target.scrollTop;

        if (distance < 5
          && (this.items.length < this.raw.goods.total)
          && !this.isExtra) {
          this.isExtra = true;
          this.footerNotice = '加载中···';
          this.bubble('selectExtraGoodsList');
        }
      },
      // 关闭兑换次数用尽提示
      cancelNotice () {
        this.isNotice = false;
      },
      exchangeGoodsList () {
        this.params.exchange = this.params.exchange == 1 ? 0 : 1;
        this.params.size = 10;
        
        if (this.params.exchange == 1) {
          this.delegationEvent = 'xs_3_294';
          this.bubble('delegationStatistics');
        }

        switch (true) {
          case !this.isSelecting:
            this.bubble('selectGoodsList');
            break;
        }
      },
      redirectGoodsDetail (id) {
        this.delegationEvent = 'xs_3_295';
        this.bubble('delegationStatistics');
        const params = { ...this.params, gid: id }
        delete params.exchange;
        this.$nextTick(() => {
          this.request('redirect', {
            path: '/student/mall/goodsdetail',
            params,
            position: this.$refs.container.scrollTop,
          });
        })
      },
      redirectOrderList () {
        this.$nextTick(() => {
          this.delegationEvent = 'xs_3_292';
          this.bubble('delegationStatistics');
          this.request('redirect', {
            path: '/student/mall/orderlist',
            params: {},
            position: this.$refs.container.scrollTop,
          });
        })
      }
    },
    mounted () {
      this.bubble('selectGoodsList');
      let id = 0;

      const payload = {};
      payload.move = (res) => {
        if (this.isFresh) {
          return false;
        }
        if (res.move > 1.2) {
          this.refresh = '松开加载～';
        }
        if (res.move < 1.2) {
          this.refresh = '下拉加载更多～';
        }
      }
      payload.end = (res) => {
        if (this.isFresh) {
          return false;
        }
        if (res.move > 1.1 && res.move < 1.3) {
          this.refresh = '刷新中～';
          this.isFresh = true;
          this.bubble('selectGoodsList');
          this.isRedirect = false;
          id++;
          this.dragRes = res;
          // setTimeout((rafId) => {
            // if (rafId == id) {
              // this.refresh = '加载成功';
            // }
          // }, 2000, id);
          // setTimeout((rafId) => {
            // if (rafId == id) {
              // res.continue();
            // }
          // }, 3000, id)
          res.pause();
          res.end();
        }
      }
      const drag = new Drag(this.$refs.items, {
        move: payload.move,
        end: payload.end,
        AMPLITUDE: 4,
        SPEED: 4,
        limit: this.$refs.container,
      });
    },
    watch: {
      'memory.selectGoodsList' (counter) {
        if (counter == 0) {
          return false;
        }
        this.$nextTick(() => {
          if (this.order.ll.limit || this.order.hf.limit) {
            this.isNotice = true;
            switch (true) {
              case this.order.ll.limit && this.order.hf.limit:
                this.notice = '本月兑换机会已用尽，下月再来吧~';
                break;
              case this.order.ll.limit:
                this.notice = '本月流量兑换机会已用尽，请下月再来充值流量~';
                break;
              case this.order.hf.limit:
                this.notice = '本月话费兑换机会已用尽，请下月再来充值话费~';
                break;
            }
          }
          if (this.user.is_exchange) {
            this.delegationEvent = 'xs_3_291';
            this.bubble('delegationStatistics');
          }
        })
        
        this.$nextTick(() => {
          this.topFixed = this.$refs.account.$el.offsetHeight;
          this.isFixed = this.$refs.container.scrollTop > this.topFixed;
        })

        this.$nextTick(() => {
          if (this.memory.position && this.isRedirect) {
            this.$refs.container.scrollTop = this.memory.position
          }
        })
        this.isLoad = true;
      }
    }
  }
</script>
<style scoped>
  .operator {
    margin-top: .6rem;
    margin-bottom: .2rem;
  }
  .items {
    position: relative;
    padding: 0 .14rem;
  }
  .item {
    padding: 0;
    box-sizing: border-box;
    text-align: center;
    padding-top: .68rem;
    border: 1px solid #eee;
    padding-bottom: .4rem;
    height: 5.2rem;
  }
  .item-content {
    display: inline-block;
  }
  .empty {
    margin-top: 1.5rem;
    width: 10rem;
    height: 6rem;
    background-size: 6rem;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url('../../assets/images/empty_good.png');
  }
  .price {
    text-align: left;
  }
</style>


