<template>
  <div id="app">
    <router-view></router-view>
    <vendor-components class="core"></vendor-components>
    <div class="vendor-components">
      <Auth /><Layer /><Redirect /><Toast />
    </div>
  </div>
</template>
<script>
  import './assets/js/flexible-all';
  import './assets/js/noscroll';
  import Auth from './components/auth';
  import Layer from './components/layer';
  import Redirect from './components/redirect';
  import Toast from './components/toast';

  export default {
    components: {
      Auth, Layer, Redirect, Toast
    },
    methods: {
      request (subscription, payload) {
        this.$store.dispatch('requestCustomer', { request: subscription, payload, page: this });
      },
    },
    mounted () {
      let innerSize = window.innerHeight;
      document.body.addEventListener('touchstart', function () {
        //
      })
      window.addEventListener('resize', () => {
        const innerReSize = window.innerHeight;
        const innerDeltaSize = innerReSize - innerSize;
        const innerDeltaSizeAbs = Math.abs(innerDeltaSize)
        innerSize = innerReSize;
        if (this.$route.path == '/student/mall/goodsdetail' && innerDeltaSizeAbs > 100 ) {
          this.request('resizekeyboard');
          this.request('resizepagepositon', {
            forward: innerDeltaSize,
          });
        }
      })
    }
  }
</script>
<style>
  @import './assets/css/app.css';
  @import './assets/css/font.css';
  @import './assets/css/btn.css';
  @import './assets/css/icon.css';
  @import './assets/css/grid.css';
  .wrap {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .container {
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    -ms-flex-negative: 1;
    flex-shrink: 1;
    -ms-flex-preferred-size: 0%;
    flex-basis: 0%;
    -webkit-overflow-scrolling: touch;
  }
</style>


