<template>
  <div class="custom" :style="{ height: content.height }">
    <nav :style="isFixed ? { position: 'fixed' } : ''" ref="content" class="content df center pr4 pl4">
      <div class="f1">
        <div class="font18 df center link cr666" v-show="isFixed">
          <div class="df center" @click="redirectGetCreditDetail">
            <span class="crGirlPink">{{ credit }}</span>翼豆
            <i class="fa-icon fa-more"></i>
          </div>
        </div>
        <div class="font16" v-show="!isFixed">全部商品</div>
      </div>
      <div class="df center" @click="selected">
        <div :class="['fa-icon', isSelected ? 'fa-radio-selected-f' : 'fa-radio-f']"></div>
        <div class="font14 cr333">我能兑换的商品</div>
      </div>
    </nav>
  </div>
</template>
<script>
  import { mapGetters } from 'vuex';
  export default {
    data () {
      return {
        content: {}, credit: 0,
      }
    },
    methods: {
      selected () {
        this.$emit('exchangeGoodsList', this.isSelected)
      },
      bubble (subscription) {
        this.$store.dispatch('bubbleDelegation', { subscription, page: this });
      },
      redirectGetCreditDetail () {
        this.delegationEvent = 'xs_3_293';
        this.bubble('delegationStatistics');
        this.bubble('delegationGetCreditDetail');
      },
    },
    computed: {
      ...mapGetters({
        memory: 'getAllocateVariable',
      })
    },
    props: ['isFixed', 'isSelected'],
    mounted () {
      if (this.isFixed) {
        this.content.height = this.$refs.content.offsetHeight;
      }
    },
    watch: {
      'memory.selectGoodsList' (counter) {
        if (counter == 0) {
          return;
        }
        this.credit = this.memory.raw.user.credit;
      }
    }
  }
</script>
<style scoped>
  .custom {
    position: relative;
    z-index: 1;
  }
  .custom .content {
    z-index: 1;
    width: 100%;
    box-sizing: border-box;
    border-bottom: .02667rem solid #eee;
    height: 1.2rem;
    background-color: #fff;
  }
  .custom .fa-radio-f, .fa-radio-selected-f {
    width: .43rem;
    height: .43rem;
    margin-right: .16rem;
    border-radius: .25rem;
  }
  .fa-radio-f {
    box-shadow: 0 0 0 .02rem #666;
  }
  .fa-radio-selected-f {
    box-shadow: 0 0 0 .03rem #3eccf5;
    background-color: #3eccf5;
    background-size: .33rem;
    background-position: .05rem .125rem;
    background-repeat: no-repeat;
  }
  .custom .fa-more {
    width: .6rem;
    height: .6rem;
    background-size: .6rem;
  }
</style>


