<template>
<div>
  <div class="toast" v-show="isToast">
    <transition-group to="div" name="fade" @after-leave="cancel">
      <div
        class="shadow"
        v-if="toast.isOn"
        v-for="(toast, index) in toasts"
        :key="index">
        {{ toast.notice }}
      </div>
    </transition-group>
  </div>
</div>
</template>
<script>
export default {
  data () {
    return {
      count: 0, isToast: true, emit: {},
      toasts: [],
      payload: {},
    }
  },
  methods: {
    bubble (subscription, page) {
      this.$store.dispatch('bubbleDelegation', { subscription, page });
    },
    register (subscription) {
      this.$store.dispatch('registerCustomer', { register: subscription, page: this });
    },
    request (subscription, payload) {
      this.$store.dispatch('requestCustomer', { request: subscription, payload, page: this });
    },
    cancel (e) {
      // console.log(e)
    },
  },
  mounted () {
    this.register('toast');
    self.toast = this;
  },
  watch: {
    count () {
      const { notice, isOn } = this.payload;
      const index = this.toasts.push({ notice, isOn });
      setTimeout(() => {
        this.toasts[index - 1].isOn = false;
      });
    }
  }
}
</script>
<style scoped>
  .toast {
    position: fixed;
    left: 2.5rem;
    top: 8rem;
  }
  .shadow {
    position: relative;
    width: 4.5rem;
    background-color: #000;
    color: #fff;
    opacity: .7;
    text-align: center;
    padding: .3rem;
    border-radius: .15rem;
    transition: opacity 3s ease-in-out;
  }
  .toast .fade-leave-to {
    opacity: 0;
  }
</style>



