const Drag = function (el, options = {}) {
  this.AMPLITUDE = options.AMPLITUDE || 2;
  this.SPEED = options.SPEED || 2;
  this.HIHER_BOUNDARY = 0;
  this.LOWER_BOUNDARY = Math.asin(1) * 0.475 * this.AMPLITUDE;

  this.limit = options.limit || null;

  this.$end = options.end;
  this.$move = options.move;

  this.moveStartY = 0;
  this.moveCurrentY = 0;
  this.moveLastY = 0;

  this.isPaused = false;
  this.isUnwatched = false;
  this.onTouch = false;

  this.compensateUp = 0;
  this.compensateDown = 0;
  this.move = 0;
  this.el = el;
  this.isTuochStart = false;
  this.isTuochEnd = false;
  this.bindTouchEvent(el);
}

Drag.prototype.moveUp = function () {
  this.compensateUp = Math.asin(this.move / this.AMPLITUDE);
  let move = this.compensateUp - ((this.moveLastY - this.moveCurrentY) / 500);
  if (this.move >= this.HIHER_BOUNDARY) {
    this.move = Math.sin(move) * this.AMPLITUDE;
  }
}

Drag.prototype.moveDown = function () {
  this.compensateDown = Math.asin(this.move / this.AMPLITUDE);
  let move = this.compensateDown + (this.moveCurrentY - this.moveLastY) / 500;
  if (move <= this.LOWER_BOUNDARY) {
    this.move = Math.sin(move) * this.AMPLITUDE;
  }
}

Drag.prototype.recover = function (watch) {
  if (this.move == 0 || !this.move) {
    return false;
  }
  let rafId;
  const raf = () => {
    if (this.isPaused) {
      rafId = requestAnimationFrame(raf);
      return false;
    }

    const compensate = Math.asin(this.move / this.AMPLITUDE);
    const move = compensate - 0.05;
    this.move = Math.sin(move) * this.AMPLITUDE;
    rafId = requestAnimationFrame(raf);
    this.el.style.top = `${this.move}rem`;

    if (typeof watch == 'function' && !this.isUnwatched) {
      watch(this);
    }

    this.move = this.move <= 0 ? 0 : this.move;

    if (this.move <= 0) {
      this.isUnwatched = false;
      cancelAnimationFrame(rafId);
    }
  }
  rafId = requestAnimationFrame(raf);
}

Drag.prototype.onTouchStart = function (e) {
  this.moveStartY = e.changedTouches[0].pageY;
}

Drag.prototype.onTouchEnd = function (e) {
  this.moveStartY = 0;
  this.moveCurrentY = 0;
  this.moveLastY = 0;

  this.compensateUp = 0;
  this.compensateDown = 0;

  this.recover(this.$end);
}

Drag.prototype.onTouchMove = function (e) {
  this.moveCurrentY = e.changedTouches[0].pageY;
  this.moveLastY = this.moveLastY == 0 ? this.moveCurrentY : this.moveLastY;
  if (this.move >= 0) {
    switch (this.moveCurrentY - this.moveLastY > 0) {
      case true:
        this.moveDown(e);
        break;
      case false:
        this.moveUp(e);
        break;
    }
    if (this.move < 0) {
      this.move = 0;
    }
    this.moveLastY = this.moveCurrentY;
    this.el.style.top = `${this.move}rem`;
    if (typeof this.$move == 'function' && !this.isUnwatched) {
      this.$move(this);
    }
  }
}

Drag.prototype.bindTouchEvent = function (el) {
  el.style.position = 'relative';

  el.addEventListener('touchstart', (e) => {
    if (this.limit.scrollTop != 0) {
      return false
    }

    this.pause();
    this.onTouch = true;
    this.onTouchStart(e)
  })

  el.addEventListener('touchmove', (e) => {
    if (this.limit.scrollTop != 0) {
      return false
    }

    this.pause();
    this.onTouchMove(e)
  })

  el.addEventListener('touchend', (e) => {
    if (this.limit.scrollTop != 0 && this.move == 0) {
      return false
    }

    this.start();
    this.onTouch = false;
    this.onTouchEnd(e)
  })
}

Drag.prototype.pause = function () {
  this.isPaused = true;
}

Drag.prototype.continue = function () {
  if (!this.onTouch) {
    this.isPaused = false;    
  }
}

Drag.prototype.start = function () {
  this.isPaused = false;
}

Drag.prototype.end = function () {
  this.isUnwatched = true;
}

export default Drag;


