import Parent from './Parent';

class Raw {
  static async selectGoodsList (page, component, id) {
    page.isSelecting = true;
    await component.$nextTick(() => {});
    const size = component.memory.pageSize || page.$route.query.size || page.params.size || 10;
    const params = { ...page.$route.query, ...page.params, type: 1, size };
    const req = Parent.bodyPack({ params, type: 'get', url: '/student/mall/goodslist' });
    Parent.get(req.query)
    .catch(() => {
      page.loadingNotice = '加载失败，返回上一页重试';
      component.request('toast', {
        notice: '请求失败，请检查网络连接',
        isOn: true,
      })
      if (page.dragRes) {
        page.refresh = '加载失败';
        setTimeout(() => {
          page.dragRes.continue();
          page.isFresh = false;
        },1000)
      }
    })
    .then((res) => {
      page.raw = res.data;
      component.allocate({ raw: res.data });
      if (page.dragRes) {
        page.refresh = '加载成功';
        setTimeout(() => {
          page.dragRes.continue();
          page.isFresh = false;
        },1000)
      }
    })
    .then(() => {
      page.items = [];
      page.items = page.raw.goods.list;
      page.order = page.raw.order;
      page.user = page.raw.user;
      page.isSelecting = false;
      setTimeout(() => {
        page.$refs.items.style.marginTop = `-${page.$refs.itemNotice.offsetHeight}px`;
      }, 100)
      page.isSelected = params.exchange == 1;
      component.resolve(id);
    })
  }

  static async selectGoodsDetail (page, component, id) {
    const params = { ...page.$route.query, type: 1 };
    const req = Parent.bodyPack({ params, type: 'get', url: '/student/mall/goodsdetail' });
    Parent.get(req.query)
    .catch(() => {
      page.loadingNotice = '加载失败，返回上一页重试';
      component.request('toast', {
        notice: '请求失败，请检查网络连接',
        isOn: true,
      })
    })
    .then((res) => {
      page.raw = res.data;
      component.modify({ payEvent: component.memory.payEvent || 0 });
      component.allocate({ raw: res.data });
    })
    .then(() => {
      page.item = page.raw.goods;
      page.user = page.raw.user;
      component.resolve(id);
      component.modify({ payEvent: component.memory.payEvent + 1 });
    })
  }

  static async exchangeGood (page, component, id) {
    const params = { ...component.memory.basic, ...component.memory.params };
    const req = Parent.bodyPack({ params, type: 'get', url: '/student/mall/exchange' });
    Parent.get(req.query)
    .catch(() => {
      component.request('toast', {
        notice: '请求失败，请检查网络连接',
        isOn: true,
      })
    })
    .then((res) => {
      if (res.status == 0) {
        component.request('redirect', {
          path: '/student/mall/exchangeresult',
          params: {
            oid: res.data.oid,
          },
        });
        page.delegationEvent = 'xs_3_297';
        page.bubble('delegationStatistics');
        component.bubble('exchangeSuccess');
      } else {
        component.request('createLayer', {
          title: '商品兑换出错',
          notice: [res.data.msg]
        });
      }
    })
  }

  static async selectExchangeResult (page, component, id) {
    const params = { ...page.$route.query, type: 1 };
    const req = Parent.bodyPack({ params, type: 'get', url: '/student/mall/exchangeresult' });
    Parent.get(req.query)
    .catch(() => {
      page.loadingNotice = '加载失败，返回上一页重试';
      component.request('toast', {
        notice: '请求失败，请检查网络连接',
        isOn: true,
      })
    })
    .then((res) => {
      page.raw = res.data;
      component.allocate({ raw: res.data });
    })
    .then(() => {
      page.order = page.raw.order;
      page.operator = { 1: '移动', 2: '联通', 4: '电信' }[page.order.ext.operator];
      component.resolve(id);
    })
  }

  static async selectOrderDetail (page, component, id) {
    const params = { ...page.$route.query, type: 1 };
    const req = Parent.bodyPack({ params, type: 'get', url: '/student/mall/orderdetail' });
    Parent.get(req.query)
    .catch(() => {
      page.loadingNotice = '加载失败，返回上一页重试';
      component.request('toast', {
        notice: '请求失败，请检查网络连接',
        isOn: true,
      })
    })
    .then((res) => {
      page.raw = res.data;
      component.allocate({ raw: res.data });
    })
    .then(() => {
      page.order = page.raw.order;
      page.item = page.order.ext.goods;
      page.operator = { 1: '移动', 2: '联通', 4: '电信' }[page.order.ext.operator];
      component.resolve(id);
    })
  }

  static async selectOrderList (page, component, id) {
    const params = { ...page.$route.query, type: 1 };
    const req = Parent.bodyPack({ params, type: 'get', url: '/student/mall/orderlist' });
    Parent.get(req.query)
    .catch(() => {
      page.loadingNotice = '加载失败，返回上一页重试';
      component.request('toast', {
        notice: '请求失败，请检查网络连接',
        isOn: true,
      })
    })
    .then((res) => {
      page.raw = res.data;
      component.allocate({ raw: res.data });
    })
    .then(() => {
      page.items = [];
      page.items = page.raw.order.list;
      page.user = page.raw.user;
      component.resolve(id);
    })
  }

  static async selectExtraGoodsList (page, component, id) {
    await component.$nextTick(() => {});
    const maxId = page.items[page.items.length - 1].id;
    const params = { ...page.$route.query, ...page.params, type: 1, max_gid: maxId };
    const req = Parent.bodyPack({ params, type: 'get', url: '/student/mall/goodslist' });
    Parent.get(req.query)
    .catch(() => {
      component.request('toast', {
        notice: '请求失败，请检查网络连接',
        isOn: true,
      })
    })
    .then((res) => {
      page.raw.goods.list = [...page.raw.goods.list, ...res.data.goods.list];
      component.allocate({ raw: page.raw });
    })
    .then(() => {
      page.items = page.raw.goods.list;
      page.isExtra = false;
      page.footerNotice = '更多商品敬请期待～';
      component.allocate({ pageSize: page.items.length });
    })
  }

  static async selectExtraOrderList (page, component, id) {
    const maxId = page.items[page.items.length - 1].id;
    const params = { ...page.$route.query, type: 1, size: 10, max_oid: maxId };
    const req = Parent.bodyPack({ params, type: 'get', url: '/student/mall/orderlist' });
    Parent.get(req.query)
    .catch(() => {
      component.request('toast', {
        notice: '请求失败，请检查网络连接',
        isOn: true,
      })
    })
    .then((res) => {
      page.raw.order.list = [...page.raw.order.list, ...res.data.order.list];
      component.allocate({ raw: page.raw });
    })
    .then(() => {
      page.items = page.raw.order.list;
      page.isExtra = false;
      component.resolve(id);
    })
  }
}
export default Raw;


