<template>
  <div class="wrap">
    <Banner :title="title" />
    <div class="container" v-show="isLoad">
      <div class="status pa3 tc">
        <div class="crSkyBlue rfont56 mt4 mb4" v-if="order.order_status == 1">充值中...</div>
        <div class="crGirlPink rfont56 mt4 mb4" v-if="order.order_status == 0">充值失败</div>
        <div class="cr333 rfont56 mt4 mb4" v-if="order.order_status == 3">充值成功</div>
        <Price class="tc df" isRow="true" :payload="item" v-if="order.order_status != 0" />
        <div class="rejected font14 tc cr333" v-if="order.order_status == 0">(翼豆已退还至账户)</div>
      </div>
      <div class="hr mb4"></div>
      <div class="pl4 pr4 df center">
        <span class="font16 cr333" @click="redirectGoodDetail">商品详情</span><i class="fa-icon fa-more"></i>
      </div>
      <div class="ml4 hr mt4"></div>
      <div class="pa4 mt1 df center item">
        <Good :payload="item" />
        <div class="ml4">
          <div class="font18 cr333">{{ order.order_name }}</div>
          <Operator class="mt3 font11" :payload="item" />
        </div>
      </div>
      <div class="meta font14 pl4 pr4 mt4">
        <p>订单编号：{{ order.order_number }}</p>
        <p>下单时间：{{ order.create_time | formatTime }}</p>
        <p>手机号码：{{ order.tel | mosaicPhone }}</p>
        <p>运营商户：中国{{ operator }}</p>
        <p>充值面额：{{ order.order_name }}</p>
      </div>
      <div class="hr mt4"></div>
      <div class="font14 failed pl4 cr999" v-if="order.order_status == 0 && order.order_category == 18">
        <div class="bold">失败的原因可能是：</div>
        <p>1.联通用户，以下地区2G用户暂不支持兑换：</p>
        <p>广西、江西、云南、天津、北京、黑龙江、河</p>
        <p>北、辽宁、四川、吉林、安徽、内蒙古</p>
        <p class="seperator"></p>
        <p>2.电信用户，内蒙古地区暂不支持兑换。</p>
        <p class="seperator"></p>
        <p>3.部分地区以下情况暂不支持兑换：</p>
        <p>天翼无线宽带套餐用户</p>
        <p>时长计费用户</p>
        <p>其它特殊号段及套餐用户</p>
        <p>欠费状态下不支持兑换</p>
      </div>
      <div class="font14 failed pl4 cr999" v-if="order.order_status == 0 && order.order_category == 19">
        <div class="bold">失败的原因可能是：</div>
        <p>1.空号、企业连号、停机、关机、欠费、无服务、锁定</p>
        <p>期、未实名登记、智 能网号码、家庭副号、携号转网、</p>
        <p>部分虚拟运营商号段等不支持充值</p>
        <p class="seperator"></p>
        <p>2.四川联通1-2元、湖北联通1-5元、广东联通智能网</p>
        <p>1-20元不支持充值，福建移动部分托收号码不支持充值。</p>
      </div>
      <div class="tc font12 cr999 copyright">最终解释权归翼课网所有</div>
    </div>
    <div class="container" v-show="!isLoad">
      <div class="tc df cr666" style="margin-top: 3rem">{{ loadingNotice }}</div>
    </div>
    
  </div>
</template>
<script scoped>
  import { mapGetters } from 'vuex';
  import { Func } from '../../vendor/lib';
  import Banner from '../../components/banner';
  import Good from '../../components/good';
  import Price from '../../components/price';
  import Operator from '../../components/operator';

  export default {
    data () {
      return {
        title: '订单详情', item: {}, raw: {}, operator: '', order: {}, isLoad: false,
        loadingNotice: '页面加载中···',
        params: {
          size: 10,
        }
      }
    },
    components: {
      Banner, Good, Price, Operator
    },
    computed: {
      ...mapGetters({
        memory: 'getAllocateVariable',
      })
    },
    filters: {
      formatTime (time) {
        return Func.formatTime(time, 'YYYY-MM-DD H:mm');
      },
      trimName (str) {
        const chips = (str + '').match(/[0-9]+/);
        if (chips) {
          return chips[0];
        }
      },
      mosaicPhone (phone) {
        if (!!phone && phone.length) {
          return phone.replace(/(\d{3})\d{4}(\d{3})/, '$1****$2');
        }
        return phone;
      }
    },
    methods: {
      bubble (subscription) {
        this.$store.dispatch('bubbleDelegation', { subscription, page: this });
      },
      request (subscription, payload) {
        this.$store.dispatch('requestCustomer', { request: subscription, payload, page: this });
      },
      redirectGoodDetail () {
        this.$nextTick(() => {
          const params = { ...this.params, gid: this.item.id }
          this.request('redirect', {
            path: '/student/mall/goodsdetail', params
          })
        });
      }
    },
    mounted () {
      this.bubble('selectOrderDetail');
    },
    watch: {
      'memory.selectOrderDetail' (counter) {
        if (counter != 0) {
          this.isLoad = true;
        }
      }
    }
  }
</script>
<style scoped>
  .item {
    border-bottom: .12rem solid #f8f8f8;
  }
  .meta p {
    margin: .05rem 0;
    color: #666;
  }
  .status {
    height: 3rem;
  }
  .fa-more {
    width: .6rem;
    height: .6rem;
    background-size: .6rem .6rem;
  }
  .copyright {
    margin-top: 1.6rem;
    margin-bottom: 3.04rem;
  }
  .failed {
    margin-top: .44rem;
  }
  .failed div {
    margin-bottom: .2rem;
  }
  .failed p {
    margin-bottom: .08rem;
  }
  .failed .seperator {
    margin-bottom: .2rem;
  }
</style>



